import React, { Component } from "react";

import { Field, ArrayField } from "../field";
import { Output } from "../output";

class InventoryFormatter extends Component {
    constructor(props) {
        super(props);
        this.state = { name: "", items: [ { amount: "", type: "", name: "", description: "", active: false }]};
        
        this.addItem = this.addItem.bind(this);
        this.removeItem = this.removeItem.bind(this);
        this.handleItemsInput = this.handleItemsInput.bind(this);
        this.handleInput = this.handleInput.bind(this);
        this.renderOutput = this.renderOutput.bind(this);
    }

    addItem() {
        const newItems = [ ...this.state.items, { amount: "", type: "", name: "", description: "", active: false }];
        this.setState({ items: newItems });
    }

    removeItem(key) {
        const newItems = this.state.items.concat();
        newItems.splice(key, 1);
        this.setState({ items: newItems });
    }

    handleItemsInput(index, name, value) {
        console.log(index, name, value);
        const newItems = this.state.items.map(
            (v, i) => (i === index ? { ...v, [name]: value } : v)
        );
        this.setState({ items: newItems });
    }

    handleInput(n, v) {
        this.setState({ [n]: v });
    }

    renderOutput() {
        const items = this.state.items.concat().sort((a, b) => {
            if (a.name < b.name || a.type < b.type) { return -1; }
            if (a.name > b.name || a.type > b.type) { return 1; }
            return 0;
        });
        const largestAmount = String(
            items.map((v) => v.amount).reduce((a, c) => Math.max(a, c))
        ).length;
        const largestType = items.map((v) => v.type).reduce((a, c) => a.length > c.length ? a : c).length;
        return `\`\`\`fix\n` +
        `[INVENTORY] ${this.state.name}\n` +
        `\`\`\`\n` +
        `\`\`\`md\n` +
        items.map((v) => `${v.active === "true" ? "#" : "*"} ${`[x${v.amount}]`.padEnd(largestAmount + 3, " ")} ${`[${v.type.toUpperCase()}]`.padEnd(largestType + 3, " ")} ${v.name}\n> ${v.description || "No description"}`).join("\n\n") +
        `\`\`\``;
    }

    render() {
        const output = this.renderOutput();
        return (
            <React.Fragment>
                <h2>Inventory Formatter</h2>
                <form>
                    <div className="form-row mb-2">
                        <div className="col">
                            <Field name="name" onChange={this.handleInput}/>
                        </div>
                    </div>
                    {
                        this.state.items.map((v, i) => (
                            <div className="form-row mb-2" key={i}>
                                <div className="col">
                                    <label>Amount</label>
                                    <div className="input-group">
                                        { i !== 0 &&
                                            <div className="input-group-prepend">
                                                <button type="button" className="btn btn-danger" onClick={() => this.removeItem(i)}>-</button>
                                            </div>
                                        }
                                        <input type="number" className="form-control" placeholder="Amount" value={this.state.items[i].amount} onChange={(e) => this.handleItemsInput(i, "amount", e.target.value)} />
                                    </div>
                                </div>
                                <div className="col">
                                    <ArrayField name="type" index={i} value={this.state.items[i].type} onChange={this.handleItemsInput} />
                                </div>
                                <div className="col">
                                    <ArrayField name="name" index={i} value={this.state.items[i].name} onChange={this.handleItemsInput} />
                                </div>
                                <div className="col">
                                    <ArrayField name="description" index={i} value={this.state.items[i].description} onChange={this.handleItemsInput} />
                                </div>
                                <div className="col">
                                <label>Active</label>
                                    <div className="input-group">
                                        <select className="custom-select" name="active" value={this.state.items[i].active} onChange={(e) => {
                                            this.handleItemsInput(i, "active", e.target.value)
                                        }}>
                                            <option value="true">Yes</option>
                                            <option value="false">No</option>
                                        </select>
                                        <div className="input-group-append">
                                            <button type="button" className="btn btn-primary" onClick={this.addItem}>+</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </form>
                <Output content={output} />
            </React.Fragment>
        );
    }
}

export { InventoryFormatter }


